.btn {
  background: #2a2a2a;
  color: #fff;
  border: none;
  position: relative;
  height: 30px;
  font-size: 17px;
  padding: 0 20px;
  cursor: pointer;
  -webkit-transition: 800ms ease all;
  transition: 800ms ease all;
  outline: none;
}
.btn:hover {
  background: #19aaad;
  color: #fff;
}
.btn:before,
.btn:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  height: 2px;
  width: 0;
  background: #19aaad;
  -webkit-transition: 400ms ease all;
  transition: 400ms ease all;
}
.btn:after {
  right: inherit;
  top: inherit;
  left: 0;
  bottom: 0;
}
.btn:hover:before,
.btn:hover:after {
  width: 100%;
  -webkit-transition: 800ms ease all;
  transition: 800ms ease all;
}
